export class PortHandler {
    constructor(port) {
        this.port = port;
    }
    startPoll(args) {
        if (this.port) {
            this.pollInterval = setInterval(() => {
                this.port.send(args.command);
            }, args.interval);
        }
    }
    stopPoll() {
        console.log('stopPoll');
        if (this.pollInterval) {
            clearInterval(this.pollInterval);
            this.pollInterval = null;
        }
    }
    raw(value) {
        this.port.send(value.endsWith('\r\n') ? value : value + '\r\n');
    }
    openPort() {
        this.port.connect();
    }
    closePort() {
        console.log('closePort');
        this.stopPoll();
        if (this.port) {
            this.port.onReceive = null;
            this.port.disconnect().then(() => { }, error => {
                console.log('error on disconnect', error);
            });
            this.port = null;
        }
    }
}
